<template>
  <div class="iframe_box">
    <div
      class="grid-img-box"
      id="masonry-grid1"
      :class="{ show: !isShowImg, hide: isShowImg }"
    >
      <div class="grid-img-item" v-for="(i, index) in iframeData" :key="index">
        <img :src="i.src" :alt="i.title" @click.prevent="selectLink(i.link)" />
        <span
          v-text="i.title"
          class="title"
          @click.prevent="selectLink(i.link)"
          style="cursor: pointer"
        ></span>
        <span v-text="i.desc"></span>
      </div>
      <div class="maskDiv" v-if="isShowImg">
        <Spin size="large" fix v-if="isShowImg"></Spin>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      iframeData: this.baseData.iframeData,
      isShowImg: true,
    };
  },
  mounted() {
    setTimeout(() => {
      this.init();
    }, 2000);
    setTimeout(() => {
      this.isShowImg = false;
    }, 3000);
  },
  methods: {
    selectLink(link) {
      this.$router.push({ name: "view", query: { link } });
    },
    init() {
      var container1 = document.querySelector("#masonry-grid1");
      var msnry = new Masonry(container1, {
        columnWidth: 20,
        itemSelector: ".grid-img-item", // 要布局的网格元素
        gutter: 20, // 网格间水平方向边距，垂直方向边距使用css的margin-bottom设置
        percentPosition: true, // 使用columnWidth对应元素的百分比尺寸
        stamp: ".grid-stamp", // 网格中的固定元素，不会因重新布局改变位置，移动元素填充到固定元素下方
        fitWidth: true, // 设置网格容器宽度等于网格宽度，这样配合css的auto margin实现居中显示
        originLeft: true, // 默认true网格左对齐，设为false变为右对齐
        originTop: true, // 默认true网格对齐顶部，设为false对齐底部
        containerStyle: { position: "relative" }, // 设置容器样式
        transitionDuration: "0.8s", // 改变位置或变为显示后，重布局变换的持续时间，时间格式为css的时间格式
        stagger: "0.03s", // 重布局时网格并不是一起变换的，排在后面的网格比前一个延迟开始，该项设置延迟时间
        resize: true, // 改变窗口大小将不会影响布局
        initLayout: true,
      });
    },
  },
};
</script>

<style lang="scss" scoped src="@/assets/scss/photoOthers.scss"></style>



